import { Link } from "@react-navigation/native";
import React from "react";
import { Text } from "react-native-web";
import DEFAULT_VALUES from "../constants/DEFAULT_VALUES";

export default React.memo(function Item({ item }) {
  return (
    <div
      className="card text-light m-auto mb-3"
      style={{ backgroundColor: "#223", maxWidth: "6.5rem" }}
    >
      <Link to={{ screen: "Download", params: { name: item.name } }}>
        <div className="bg-secondary rounded" style={{ height: 100 }}>
          <img
            src={item.iconUrl}
            alt={item.name}
            width={"100%"}
            height={"100%"}
            className={"rounded card-img-top"}
          />
        </div>
        <div className="card-body">
          <Text style={{ color: DEFAULT_VALUES.text }} numberOfLines={2}>
            {item.name}
          </Text>
          <Text style={{ color: DEFAULT_VALUES.text }} numberOfLines={1}>
            {item.size}
          </Text>
        </div>
      </Link>
    </div>
  );
});
