import React from "react";
import DEFAULT_VALUES from "../constants/DEFAULT_VALUES";
import { Text } from "react-native-web";
import { useSettings } from "../hooks/Settings.context";

function Burmese({ str, className, style, onPress }) {
  const { font } = useSettings();

  const text = React.useMemo(
    () => (str === 0 ? 0 : knayi.fontConvert(str, font)),
    [str, font]
  );

  return (
    <div
      onClick={onPress}
      className={className}
      style={{ color: DEFAULT_VALUES.text, ...style }}
    >
      {text}
    </div>
  );
}

const BurmeseText = ({ str, style, numberOfLines, onPress }) => {
  const { font } = useSettings();

  const text = React.useMemo(
    () => (str === 0 ? 0 : knayi.fontConvert(str, font)),
    [str, font]
  );
  return (
    <Text
      numberOfLines={numberOfLines}
      onPress={onPress}
      style={[{ color: DEFAULT_VALUES.text, fontSize: 16 }, style]}
    >
      {text}
    </Text>
  );
};

Burmese.Text = BurmeseText;
export default Burmese;
