import { useNavigation } from "@react-navigation/native";
import React from "react";
import DEFAULT_VALUES from "../../constants/DEFAULT_VALUES";
import Icon from "../../elements/Icon";
import { useSettings } from "../../hooks/Settings.context";

export default function MainNavigator({ goBack }) {
  const { changeFont, font } = useSettings();
  const navigation = useNavigation();

  const onGoBack = React.useCallback(() => {
    navigation.canGoBack() ? navigation.goBack() : navigation.navigate("Home");
  }, []);

  return (
    <nav
      className="d-flex flex-row justify-content-between align-items-center bg-dark text-light shadow border-bottom p-2"
      style={{ height: DEFAULT_VALUES.navHeight }}
    >
      {goBack ? (
        <Icon.Btn
          onPress={onGoBack}
          name={"arrow_back"}
          info={"Go Back To Main Page"}
          color={"#eee"}
        />
      ) : (
        <span></span>
      )}
      <div className="navbar-brand h5 mb-0">
        My<i className="text-decoration-line-through ps-1">anamr</i> App Store
      </div>
      <button
        className="btn btn-sm btn-dark border text-capitalize"
        onClick={changeFont}
      >
        {font}
      </button>
    </nav>
  );
}
