import React from "react";
import Item from "../components/Item";
import Layout from "../elements/Layout";
import MainNavigator from "../navigation/navigators/MainNavigator";
import itemList from "../constants/ItemList";

export default function Home() {
  const renderItem = React.useCallback((item, index) => (
    <Item item={itemList[item]} key={index} />
  ));

  return (
    <Layout>
      <MainNavigator />
      <div className="d-flex align-items-start justify-content-start py-3 p-2 flex-wrap">
        {Object.keys(itemList).map(renderItem)}
      </div>
    </Layout>
  );
}
