//settings always has 's'
import React from "react";
import useLocalStorage from "./useLocalStorage";
import { useWindowDimensions } from "react-native-web";
import { reducerSettings, initialSettings } from "../utils/settings.utils";

// creating variables
const settingsContext = React.createContext();

export const useSettings = () => React.useContext(settingsContext);

export function SettingsContextProvider({ children }) {
  const [settings, dispatch] = React.useReducer(
    reducerSettings,
    initialSettings
  );
  const { getItem, setItem } = useLocalStorage("@settings");

  const readFromStorage = async () => {
    const item = await getItem();
    if (item) {
      dispatch({ type: "restore", payload: item });
    }
  };

  React.useEffect(async () => await readFromStorage(), []);
  React.useEffect(async () => await setItem(settings), [settings]);

  //change font
  const changeFont = React.useCallback(() => {
    dispatch({ type: "font" });
  }, []);

  //responsive width
  const { width } = useWindowDimensions();
  const device = React.useMemo(() => (width >= 600 ? "lg" : "sm"), [width]);

  const value = {
    font: settings.font,
    changeFont,
    device,
  };

  return (
    <settingsContext.Provider value={value}>
      {children}
    </settingsContext.Provider>
  );
}
