import React from "react";
import DEFAULT_VALUES from "../constants/DEFAULT_VALUES";

function Icon({
  name,
  size = DEFAULT_VALUES.iconSize,
  color = "inherit",
  info,
  className = "",
}) {
  return (
    <span
      className={"material-icons" + className}
      title={info || name}
      style={{ fontSize: size + "px", color: color }}
    >
      {name}
    </span>
  );
}

function IconAsBtn({
  name,
  size = DEFAULT_VALUES.iconSize,
  info,
  className = "text-light pt-2",
  iconClassName = "",
  onPress,
  color = "inherit",
}) {
  return (
    <div onClick={onPress} className={"btn btn-sm " + className}>
      <Icon
        name={name}
        size={size}
        info={info}
        color={color}
        className={iconClassName}
      />
    </div>
  );
}

Icon.Btn = IconAsBtn;
export default Icon;
