export const initialSettings = { font: "unicode" };

export const reducerSettings = (state, action) => {
  switch (action.type) {
    case "restore":
      return { ...state, ...action.payload };
    case "font":
      return {
        ...state,
        font: state.font === "unicode" ? "zawgyi" : "unicode",
      };
    default:
      throw new Error();
  }
};
