import React from "react";
import { FlatList } from "react-native-web";
import DEFAULT_VALUES from "../constants/DEFAULT_VALUES";
import Image from "../elements/Image";
import Burmese from "../elements/Burmese";
import { getBurmeseGenre, translateNumbers } from "../utils/utils";

export default function ItemDetails({ item }) {
  return (
    <div className="container py-2">
      <div className="row">
        <div className="col-lg-6 col-12">
          <ItemHeader item={item} />
          <ItemDescription item={item} />
          <ItemScreenShots item={item} />
        </div>
        <div className="col-lg-6 col-12">
          <ItemInformation item={item} />
        </div>
      </div>
    </div>
  );
}

const ItemHeader = React.memo(({ item }) => (
  <div className="d-flex align-items-center justify-content-start mb-3">
    <Image
      url={item.iconUrl}
      height={48}
      className={"rounded shadow"}
      width={48}
    />
    <div className="ps-3 flex-grow-1">
      <Burmese.Text
        str={item.name}
        numberOfLines={1}
        style={{ fontSize: 18 }}
      />
      <div>
        <i>version 1.0.0</i>
        <small className="ms-2">(April 14, 2022)</small>
      </div>
    </div>
    <a
      href={item.downloadUrl}
      className="btn btn-primary btn-sm self-align-end"
    >
      Download
    </a>
  </div>
));

const ItemDescription = React.memo(({ item }) => {
  const [line, setLine] = React.useState(3);

  const collapseText = () => setLine(line ? undefined : 3);

  return (
    <div className="mb-3">
      <div
        className="fw-bold butt py-2"
        title={line ? "click to see more" : "click to see less"}
        onClick={collapseText}
      >
        <Burmese str={"ေဆာ့၀ဲ အေၾကာင္း"} />
      </div>
      <Burmese.Text
        numberOfLines={line}
        onPress={collapseText}
        str={item.description}
      />
    </div>
  );
});

const ItemScreenShots = React.memo(({ item }) => {
  const renderItem = ({ item }) => (
    <Image
      url={item}
      height={DEFAULT_VALUES.screenshotHeight}
      width={300 * (9 / 16)}
      className={"me-2"}
    />
  );

  return (
    <div className="mb-3">
      <FlatList
        data={item.screenshots}
        horizontal={true}
        showsHorizontalIndicator={true}
        renderItem={renderItem}
        keyExtractor={(_, index) => index.toString()}
      />
    </div>
  );
});

const ItemInformation = React.memo(({ item }) => {
  const featureItem = React.useCallback(
    (val, index) => <li key={index}>{val}</li>,
    []
  );

  return (
    <div>
      <div className="fw-bold py-2">
        <Burmese str={"ထူးျခားခ်က္မ်ား"} />
      </div>
      <ul>{item.features.map(featureItem)}</ul>
      <div className="list-group">
        <div className="list-group-item bg-dark">
          <div className="row">
            <Burmese className="col-6" str={"Download"} />
            <div className="col-6">
              <a href={item.downloadUrl} className="btn btn-primary btn-sm">
                Download
              </a>
            </div>
          </div>
        </div>
        <ListItem name={"တန္ဖိုး"} value={item.price} />
        <ListItem name={"အရြယ္အစား"} value={translateNumbers(item.size)} />
        <ListItem name={"ဗားရွင္း"} value={item.version} />
        <ListItem
          name={"အမ်ိဳးအစား"}
          value={item.genre.map((val) => getBurmeseGenre(val)).join(", ")}
        />
        <ListItem
          name={"ေၾကာ္ျငာမ်ား"}
          className={item.ads ? "text-danger" : "text-success"}
          value={item.ads ? "ပါသည္" : "မပါပါ"}
        />
        <ListItem
          name={"ထုတ္လုပ္သည့္ အခ်ိန္"}
          value={
            typeof item.timestamp === "string"
              ? item.timestamp
              : item.timestamp.toDate().toDateString()
          }
        />
        <ListItem name={"DEVELOPER"} value={item.developer} />
      </div>
    </div>
  );
});

const ListItem = ({ name, value, className }) => (
  <div className="list-group-item bg-dark">
    <div className="row">
      <Burmese className="col-6" str={name} />
      <Burmese className={"col-6 " + className} str={value} />
    </div>
  </div>
);

/*
name: String
icon: String URL
downloadUrl: String URL
version: String
size: Number
description: String
features: String
screenshots: ["url","url","url"]
previousVersions: [
  {name: String,
  icon: String URL,
downloadUrl: String URL,
version: String,
size: Number,
description: String,
features: String}
]
*/
