export const translateNumbers = (val) => {
  const singleNumbers = val
    .split("")
    .map((item) => getBurmeseNumber(item))
    .join("");

  return singleNumbers;
};

const getBurmeseNumber = (val) => {
  switch (val) {
    case "0":
      return "၀";
    case "1":
      return "၁";
    case "2":
      return "၂";
    case "3":
      return "၃";
    case "4":
      return "၄";
    case "5":
      return "၅";
    case "6":
      return "၆";
    case "7":
      return "၇";
    case "8":
      return "၈";
    case "9":
      return "၉";

    default:
      return val;
  }
};

export const getBurmeseGenre = (val) => {
  switch (val) {
    case "Audio":
      return "အသံ";
    case "Book":
      return "စာအုပ္";
    case "18+":
      return "၁၈+";
  }
};
